<template>
  <div class="mqtt-data">
    <div class="enable-toggle">
      <label for="enable-switch">Enable MQTT</label>
      <label class="switch">
        <input
          id="enable-switch"
          type="checkbox"
          v-model="config.enabled"
          @change="onInputChange"
        />
        <span class="slider"></span>
      </label>
    </div>

    <div class="settings" v-if="config.enabled">
      <div class="field">
        <label for="broker">Broker Address:</label>
        <input
          type="text"
          id="broker"
          v-model="config.broker"
          @input="onInputChange"
          placeholder="mqtt.example.com"
        />
      </div>

      <div class="field">
        <label for="port">Port Number:</label>
        <input
          type="number"
          id="port"
          v-model.number="config.port"
          @input="onInputChange"
          placeholder="1883"
        />
      </div>

      <div class="field">
        <label for="login">Login:</label>
        <input
          type="text"
          id="login"
          v-model="config.login"
          @input="onInputChange"
          placeholder="username"
        />
      </div>

      <div class="field">
        <label for="password">Password:</label>
        <input
          type="password"
          id="password"
          v-model="config.password"
          @input="onInputChange"
          placeholder="â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢"
        />
      </div>
    </div>

    <div class="button-row">
      <button
        v-if="showSaveButton"
        type="button"
        @click="saveConfig"
      >
        {{ saveButtonLabel }}
      </button>
      <!-- <button
        type="button"
        @click="$emit('close')"
      >
        CANCEL
      </button> -->
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'

// Emit only 'close' so that SettingsModal can close the MQTT section
const emit = defineEmits(['close'])

// Reactive MQTT configuration object
const config = ref({
  enabled: false,
  broker: '',
  port: '',
  login: '',
  password: ''
})

const showSaveButton = ref(false)
const saveButtonLabel = ref('SEND CONFIG')
const message = ref('')

// Fetch existing MQTT configuration on mount
async function fetchConfig() {
  try {
    const response = await fetch('/api/v1/mqtt')
    if (!response.ok) {
      throw new Error('Failed to fetch configuration')
    }
    const data = await response.json()
    config.value.enabled = data.enabled
    config.value.broker = data.broker
    config.value.port = data.port
    config.value.login = data.login
    config.value.password = data.password
  } catch (error) {
    message.value = error.message
  }
}

// Show SAVE button when any input changes
function onInputChange() {
  showSaveButton.value = true
}

// Save configuration via POST
async function saveConfig() {
  try {
    const payload = {
      enabled: config.value.enabled,
      broker: config.value.broker,
      port: config.value.port,
      login: config.value.login,
      password: config.value.password
    }
    const response = await fetch('/api/v1/mqtt', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload)
    })
    if (!response.ok) {
      const errText = await response.text()
      throw new Error(errText || 'Failed to save configuration')
    }
    message.value = 'Configuration saved successfully'
    saveButtonLabel.value = 'SAVED'
    showSaveButton.value = false
  } catch (error) {
    message.value = error.message
  }
}

onMounted(() => {
  fetchConfig()
})
</script>

<style scoped>

.enable-toggle {
  display: flex;
  flex-direction: column;
  margin-bottom: 12px;
}

.enable-toggle label:first-of-type {
  color: #333;
  font-size: 0.6em;
  text-transform: uppercase;
  margin-bottom: 14px;
  font-weight: bold;
}

.switch {
  position: relative;
  margin-top: 0;
  width: 50px;
  height: 24px;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0; left: 0; right: 0; bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 24px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

.switch input:checked + .slider {
  background-color: #2196F3;
}

.switch input:checked + .slider:before {
  transform: translateX(26px);
}

.field {
  margin-bottom: 12px;
}

.field label {
  display: block;
  font-size: 0.6em;
  text-transform: uppercase;
  margin-bottom: 4px;
  color: #333;
  font-weight: bold;
}

.field input[type="text"],
.field input[type="number"],
.field input[type="password"] {
  display: block;
  padding: 10px 6px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #9c9c9c;
  color: #383838;
}

.button-row {
  display: flex;
  /* justify-content: flex-end; */
  gap: 10px;
  margin-top: 10px;
}

.button-row button {
  background: #40464e;
  border: none;
  width: 110px;
  padding: 10px 16px;
  color: white;
  border-radius: 20px;
  font-size: 0.7em;
  text-transform: uppercase;
  cursor: pointer;
}

.button-row button:hover {
  opacity: 0.9;
}
</style>
