<template>
  <div class="time-check">
    <!-- Display the current time data -->
    <div v-if="timeData" class="current-time-style">
      <p class="time">
        {{ timeData.hour }}:{{ timeData.minute }}:{{ timeData.second }}&nbsp;
        {{ timeData.day }}.{{ timeData.month }}.{{ timeData.year }}
        <span v-if="is12HourFormat">{{ timeData.period }}</span>
      </p>
    </div>

    <!-- Dropdown list for selecting the timezone -->
    <label for="timezone-select">Select Timezone: </label>
    <select id="timezone-select" v-model="selectedTimezone" @change="updateTime">
      <option v-for="tz in timezones" :key="tz" :value="tz">
        {{ tz }}
      </option>
    </select>

    <!-- Set offset (optional) -->
    <div class="offset">
      <label>Offset (if needed)</label>
      <input
        type="number"
        v-model="offset"
        min="-23"
        max="23"
        @change="onOffsetChange"
      />
    </div>

    <!-- Toggle switch for 12h/24h format -->
    <div class="format-toggle">
      <label for="format-toggle">Time format (12/24h): </label>
      <label class="switch">
        <input
          id="format-toggle"
          type="checkbox"
          v-model="is12HourFormat"
          @change="onFormatChange"
        />
        <span class="slider"></span>
      </label>
    </div>

    <!-- Dropdown for selecting unit, emit to parent -->
    <div class="unit-select">
      <label for="unit-select">Select unit to display: </label>
      <select
        id="unit-select"
        v-model="selectedUnit"
        @change="emit('unitSelected', selectedUnit)"
      >
        <option v-for="u in units" :key="u" :value="u">
          {{ u }}
        </option>
      </select>
    </div>
  </div>
</template>

<script setup>
import {
  ref,
  onMounted,
  onUnmounted,
  defineProps,
  defineEmits,
  defineExpose
} from 'vue'

const props = defineProps({
  initialConfig:        {},
  initialGeneralConfig: { type: Object, default: () => ({}) }
})

// Emit events to parent when unit, format, timezone or offset changes
const emit = defineEmits([
  'unitSelected',
  'formatSelected',
  'timezoneSelected',
  'offsetSelected'
])

// Expose the sendTimeArray method to the parent
defineExpose({ sendTimeArray })

// List of supported timezones
const timezones = ref([])
const selectedTimezone = ref('')

// 12h/24h format toggle
const is12HourFormat = ref(false)

// Offset in hours
const offset = ref(0)

// Unit selection (unused internally, just emitted)
const units = ['hours', 'minutes', 'seconds', 'days', 'months', 'years']
const selectedUnit = ref(units[0])

// Reactive object holding the split-up current time parts
const timeData = ref(null)
let timer = null

// Update only the displayed time (no network call)
const refreshTime = () => {
  // 1) calculate current timestamp with offset applied
  const nowWithOffset = Date.now() + offset.value * 60 * 60 * 1000
  const date = new Date(nowWithOffset)

  // 2) build an Intl formatter that also includes seconds
  const formatter = new Intl.DateTimeFormat('en-US', {
    timeZone: selectedTimezone.value,
    year:     'numeric',
    month:    '2-digit',
    day:      '2-digit',
    hour:     '2-digit',
    minute:   '2-digit',
    second:   '2-digit',
    hour12:   is12HourFormat.value
  })

  // 3) break the formatted string into parts
  const parts = formatter.formatToParts(date)
  const data = {}
  parts.forEach(part => {
    switch (part.type) {
      case 'year':      data.year   = part.value; break
      case 'month':     data.month  = part.value; break
      case 'day':       data.day    = part.value; break
      case 'hour':      data.hour   = part.value; break
      case 'minute':    data.minute = part.value; break
      case 'second':    data.second = part.value; break
      case 'dayPeriod': data.period = part.value; break
      // ignore literals
    }
  })

  // 4) write into the reactive object
  timeData.value = data
}

// Called when the user picks a new timezone
const updateTime = () => {
  refreshTime()
  emit('timezoneSelected', selectedTimezone.value)
}

// Called when the user toggles 12h/24h format
const onFormatChange = () => {
  refreshTime()
  emit('formatSelected', is12HourFormat.value ? '12h' : '24h')
}

// Called when the user changes the offset
const onOffsetChange = () => {
  refreshTime()
  emit('offsetSelected', offset.value)
}

// Method exposed to parent: send the time array (including seconds) to ESP32
async function sendTimeArray() {
  // rebuild the date exactly as in refreshTime()
  const nowWithOffset = Date.now() + offset.value * 60 * 60 * 1000;
  const date = new Date(nowWithOffset);

  // ask Intl for a 24-hour hour, ignoring the 12/24 toggle
  const hour24 = new Intl.DateTimeFormat('en-US', {
    timeZone: selectedTimezone.value,
    hour:     '2-digit',
    hour12:   false
  }).format(date);

  // now build your array, using `hour24` instead of `timeData.value.hour`
  const fullYear    = parseInt(timeData.value.year, 10);
  const twoDigitYear = fullYear % 100;      // np. 2025 % 100 = 25

  const timeArray = [
    parseInt(timeData.value.second,10),
    parseInt(timeData.value.minute, 10),
    parseInt(hour24,               10),   // â† always 00â€“23
    parseInt(timeData.value.day,   10),
    parseInt(timeData.value.month, 10),
    twoDigitYear
  ];

  console.log('Sending timeArray:', timeArray);

  try {
    const response = await fetch('/api/v1/clock', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(timeArray)
    })
    if (!response.ok) throw new Error('Network response was not ok')
    console.log('Time data sent successfully')
  } catch (error) {
    console.error('Error sending time data:', error)
  }
}

onMounted(() => {
  // Populate timezone list (use full Intl if available)
  if (typeof Intl.supportedValuesOf === 'function') {
    timezones.value = Intl.supportedValuesOf('timeZone')
  } else {
    timezones.value = [
      'Europe/London',
      'Europe/Amsterdam',
      'America/New_York',
      'Asia/Tokyo',
      'Australia/Sydney'
    ]
  }

  // Pick the first timezone by default
  if (timezones.value.length) {
    selectedTimezone.value = timezones.value[0]
    updateTime()
  }

  // Start a 1-second interval to update the display every second
  timer = setInterval(refreshTime, 1000)

  // Apply any saved general config from props
  const gen = props.initialGeneralConfig
  if (gen.time_zone && timezones.value.includes(gen.time_zone)) {
    selectedTimezone.value = gen.time_zone
  } else {
    selectedTimezone.value = timezones.value[0]
  }
  emit('timezoneSelected', selectedTimezone.value)

  // Apply config overrides if present
  const cfg = props.initialConfig
  offset.value = typeof cfg.offset === 'number' ? cfg.offset : 0
  emit('offsetSelected', offset.value)

  if (cfg.time_format === '12h')      is12HourFormat.value = true
  else if (cfg.time_format === '24h') is12HourFormat.value = false
  emit('formatSelected', is12HourFormat.value ? '12h' : '24h')

  if (cfg.clock_unit && units.includes(cfg.clock_unit)) {
    selectedUnit.value = cfg.clock_unit
  }
  emit('unitSelected', selectedUnit.value)
})

onUnmounted(() => {
  clearInterval(timer)
})
</script>

<style scoped>
.format-toggle,
.unit-select {
  display: flex;
  flex-direction: column;
}

.switch {
  position: relative;
  margin-top: 0;
  width: 50px;
  height: 24px;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0; left: 0; right: 0; bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 24px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

.switch input:checked + .slider {
  background-color: #2196F3;
}

.switch input:checked + .slider:before {
  transform: translateX(26px);
}

.current-time-style {
  text-align: center;
}

.current-time-style .time {
  font-weight: bold;
  font-size: 1.5rem;
  margin: 0 auto;
  margin-top: 20px;
}

label {
  color: #333;
  margin: 25px 0 15px;
  font-size: 0.6em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
}
</style>
