<template>
  <div class="backdrop" @click.self="closeModal">
    <div class="update-check">
      <h3>Versions</h3>
      <div class="label">
        <p>
          Firmware:
          {{ installedFirmwareVersion }}
          <span v-if="firmwareState === 'update'" style="color: green">
            ({{ availableFirmwareVersion }})
          </span>
          <span v-else-if="firmwareState === 'offline'" style="color: red">
            (N/A)
          </span>
          |
          Web App:
          {{ installedWebAppVersion }}
          <span v-if="webAppState === 'update'" style="color: green">
            ({{ availableWebAppVersion }})
          </span>
          <span v-else-if="webAppState === 'offline'" style="color: red">
            (N/A)
          </span>
        </p>
      </div>

      <div v-if="!showProgress">
        <button v-if="updateAvailable" @click="startUpdate">
          UPDATE AVAILABLE
        </button>
      </div>

      <div class="progress-container" v-if="showProgress">
        <div class="label">{{ updateLabel }}</div>
        <div class="progress-wrapper">
          <progress max="100" :value="progress"></progress>
          <div class="progress-percent">{{ progress }}%</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'

// Installed version values
const installedFirmwareVersion = ref('')
const installedWebAppVersion = ref('')
// Available (remote) version values
const availableFirmwareVersion = ref('')
const availableWebAppVersion = ref('')
// Whether an update is available
const updateAvailable = ref(false)
// State indicators: '', 'update', 'offline'
const firmwareState = ref('')
const webAppState = ref('')
// Label shown above progress bar
const updateLabel = ref('')
// Progress percentage (0â€“100)
const progress = ref(0)
// Whether to show the progress section
const showProgress = ref(false)
// Flag to detect if update has started
const updateInProgress = ref(false)

const emit = defineEmits(['close'])

function closeModal() {
  emit('close')
}

// Fetch currently installed versions from the ESP32
async function fetchInstalledVersions() {
  try {
    const response = await fetch('/api/v1/versions')
    const data = await response.json()
    installedFirmwareVersion.value = data.versions.firm_version
    installedWebAppVersion.value = data.versions.web_app_version
  } catch {
    firmwareState.value = 'offline'
    webAppState.value = 'offline'
  }
  console.log('Firmware Version:', installedFirmwareVersion.value);
  console.log('WebApp Version:', installedWebAppVersion.value);
}

// Fetch available versions from the OTA server
async function fetchAvailableVersions() {
  try {
    const response = await fetch(
      'https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/version.json'
    )
    const data = await response.json()
    availableFirmwareVersion.value = data.firmware_version
    availableWebAppVersion.value = data.web_app_version
  } catch {
    firmwareState.value = 'offline'
    webAppState.value = 'offline'
    return
  }
  firmwareState.value =
    installedFirmwareVersion.value !== availableFirmwareVersion.value
      ? 'update'
      : ''
  webAppState.value =
    installedWebAppVersion.value !== availableWebAppVersion.value
      ? 'update'
      : ''
  updateAvailable.value =
    firmwareState.value === 'update' || webAppState.value === 'update'
}

// Check if any component needs updating
function checkForUpdate() {
  if (
    (installedFirmwareVersion.value !== availableFirmwareVersion.value ||
      installedWebAppVersion.value !== availableWebAppVersion.value) &&
    firmwareState.value !== 'offline' &&
    webAppState.value !== 'offline'
  ) {
    updateAvailable.value = true
  } else {
    updateAvailable.value = false
  }
}

// Poll OTA progress endpoint every second
let progressInterval = null
function startProgressPolling() {
  progressInterval = setInterval(async () => {
    try {
      const response = await fetch('/api/v1/ota/progress')
      const data = await response.json()
      progress.value = data.progress

      if (progress.value > 0 && !updateInProgress.value) {
        updateInProgress.value = true
      }

      // When progress returns to 0 after an update, reload the page
      if (updateInProgress.value && progress.value === 0) {
        clearInterval(progressInterval)
        updateInProgress.value = false
        window.location.reload()
      }
    } catch {
      // Ignore polling errors
    }
  }, 1000)
}

// Initiate OTA update for firmware, web app, or both
async function startUpdate() {
  showProgress.value = true
  progress.value = 0

  const needsFirmwareUpdate =
    installedFirmwareVersion.value !== availableFirmwareVersion.value
  const needsWebAppUpdate =
    installedWebAppVersion.value !== availableWebAppVersion.value

  if (needsFirmwareUpdate && needsWebAppUpdate) {
    updateLabel.value = 'Updating Web-App and Firmware...'
    try {
      const response = await fetch('/api/v1/ota/both', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          firmware_url: `https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/firmware_v${availableFirmwareVersion.value}.bin`,
          web_app_url: `https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/web_app_v${availableWebAppVersion.value}.bin`
        })
      })
      await response.text()
      startProgressPolling()
    } catch {
      // Handle error silently
    }
  } else if (needsFirmwareUpdate) {
    updateLabel.value = 'Updating firmware...'
    try {
      const response = await fetch('/api/v1/ota/firmware', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          url: `https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/firmware_v${availableFirmwareVersion.value}.bin`
        })
      })
      await response.text()
      startProgressPolling()
    } catch {
      // Handle error silently
    }
  } else if (needsWebAppUpdate) {
    updateLabel.value = 'Updating Web-App...'
    try {
      const response = await fetch('/api/v1/ota/web_app', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          url: `https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/web_app_v${availableWebAppVersion.value}.bin`
        })
      })
      await response.text()
      startProgressPolling()
    } catch {
      // Handle error silently
    }
  }
}

onMounted(async () => {
  await fetchInstalledVersions()
  await fetchAvailableVersions()
  checkForUpdate()
})
</script>

<style scoped>
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
}

.update-check {
  max-width: 420px;
  margin: 150px auto;
  background: #ffffff;
  text-align: left;
  padding: 40px;
  border-radius: 10px;
}

.label {
  color: #333333;
  font-size: 0.7em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
  margin-bottom: 10px;
}

h3 {
  color: #333333;
  margin: 0 0 5px;
  font-size: 0.9em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
}

button {
  background: #40464e;
  border: none;
  padding: 10px 20px;
  margin-top: 20px;
  color: #ffffff;
  border-radius: 20px;
  width: 148px;
  cursor: pointer;
  font-size: 0.7em;
  text-transform: uppercase;
}

.progress-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 16px;
}

.progress-wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 8px;
}

progress {
  width: 390px;
  height: 10px;
  border: none;
  border-radius: 5px;
  -webkit-appearance: none;
  appearance: none;
}

/* Chrome, Safari, Opera */
progress::-webkit-progress-bar {
  background-color: #e6e6e6;
  border-radius: 10px;
}

progress::-webkit-progress-value {
  background-color: #333333;
  border-radius: 10px;
}

/* Firefox */
progress::-moz-progress-bar {
  background-color: #333333;
  border-radius: 10px;
}

.progress-percent {
  font-size: 0.7em;
  color: #333333;
}
</style>
