<template>
  <div class="backdrop" @click.self="closeModal">
    <div class="wifi-config">
      <h3>Wi-Fi Configuration</h3>
      <div>
        <label>Current Mode: {{ mode }}</label>
      </div>
      <div class="wifi-mode">
        <input
          type="radio"
          id="ap"
          value="AP"
          v-model="selectedMode"
        />
        <label for="ap">Access Point</label>
        <br />
        <input
          type="radio"
          id="sta"
          value="STA"
          v-model="selectedMode"
        />
        <label for="sta">Wi-Fi Client</label>
      </div>
      <div v-if="selectedMode === 'STA'">
        <div>
          <br />
          <label for="ssid">SSID:</label>
          <input
            type="text"
            id="ssid"
            v-model="ssid"
          />
        </div>
        <div>
          <label for="password">Password:</label>
          <input
            type="password"
            id="password"
            v-model="password"
          />
        </div>
      </div>
      <button @click="updateConfig">SAVE</button>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'

// Reactive variables for current mode, selected mode, SSID, and password
const mode = ref('')
const selectedMode = ref('AP')
const ssid = ref('')
const password = ref('')

const emit = defineEmits(['close'])

function closeModal() {
  emit('close')
}

// Fetch current configuration from GET /api/v1/mode
async function fetchConfig() {
  try {
    const response = await fetch('/api/v1/mode')
    if (response.ok) {
      const data = await response.json()
      mode.value = data.mode
      selectedMode.value = data.mode
      ssid.value = data.ssid || ''
      // For security reasons, password is not returned by the API; leave it empty
      password.value = ''
    }
  } catch {
    console.error('Error fetching configuration')
  }
}

// Update configuration via POST /api/v1/mode; include SSID and password in STA mode
async function updateConfig() {
  const payload = { mode: selectedMode.value }
  if (selectedMode.value === 'STA') {
    payload.ssid = ssid.value
    payload.password = password.value
  }
  try {
    const response = await fetch('/api/v1/mode', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload)
    })
    if (response.ok) {
      const message = await response.text()
      alert(message)
    } else {
      alert('Failed to update configuration')
    }
  } catch {
    console.error('Error updating configuration')
    alert('Error updating configuration')
  }
}

onMounted(() => {
  fetchConfig()
})
</script>

<style scoped>
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
}

.wifi-config {
  max-width: 420px;
  margin: 150px auto;
  background: #ffffff;
  padding: 40px;
  border-radius: 10px;
}

h3 {
  color: #333333;
  margin: 0 0 15px;
  font-size: 0.9em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
}

label {
  color: #333333;
  margin: 25px 0 15px;
  font-size: 0.6em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
}

input,
select {
  display: block;
  padding: 10px 6px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #9c9c9c;
  color: #383838;
}

input[type='radio'] {
  display: inline-block;
  accent-color: #333333;
  width: 16px;
  margin: 5px 10px 0 0;
  position: relative;
  top: 2px;
}

button {
  background: #333333;
  border: none;
  padding: 10px 20px;
  margin-top: 20px;
  color: #ffffff;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.7em;
  text-transform: uppercase;
}

.wifi-mode {
  margin: 15px 0;
}
</style>
