<template>
  <div class="backdrop" @click.self="closeModal">
    <div class="settings">
      <h3>Settings</h3>

      <div class="option">
        <label>LED</label>
        <label class="switch">
          <input type="checkbox" v-model="ledState" />
          <span class="slider"></span>
        </label>
      </div>

      <button @click.self="closeModal">OK</button>
    </div>
  </div>
</template>

<script setup>
// Import necessary functions from Vue 3 Composition API
import { ref, watch, onMounted } from 'vue'

// Define the emit event for closing the modal
const emit = defineEmits(['close'])

// Create a reactive variable to hold the LED state (true/false)
const ledState = ref(false)

/**
 * Fetch the current LED status from the server.
 * Endpoint: GET /api/v1/led
 * Expected response: { status: boolean }
 */
async function fetchLedState() {
  try {
    const response = await fetch('/api/v1/led')
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`)
    }
    const data = await response.json()
    // Update local state with the value returned by the server
    ledState.value = data.status
  } catch (error) {
    console.error('Failed to fetch LED state:', error)
  }
}

/**
 * Send the new LED status to the server.
 * Endpoint: POST /api/v1/led
 * Request body: { status: boolean }
 */
async function updateLedState(newState) {
  try {
    const response = await fetch('/api/v1/led', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ status: newState })
    })
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`)
    }
    // Optionally, you could read the response if server returns updated object
    // const result = await response.json()
  } catch (error) {
    console.error('Failed to update LED state:', error)
  }
}

/**
 * Watcher: whenever ledState changes, send a POST to update the server.
 */
watch(ledState, (newVal, oldVal) => {
  updateLedState(newVal)
})

/**
 * Emit the 'close' event to parent component to close the modal.
 */
function closeModal() {
  emit('close')
}

/**
 * On component mount, fetch the initial LED state from the server
 * so that the checkbox renders correctly.
 */
onMounted(() => {
  fetchLedState()
})
</script>

<style scoped>
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
}

.settings {
  max-width: 420px;
  margin: 150px auto;
  background: #ffffff;
  padding: 40px;
  border-radius: 10px;
}

h3 {
  color: #333333;
  margin: 0 0 15px;
  font-size: 0.9em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
}

label {
  color: #333333;
  margin: 25px 0 15px;
  font-size: 0.6em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
}

.option {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 15px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #6c757d;
  transition: 0.4s;
  border-radius: 20px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

.switch input:checked + .slider {
  background-color: #007bff;
}

.switch input:checked + .slider:before {
  transform: translateX(20px);
}
</style>
