<template>
  <div class="container">
    <button class="submit" @click="sendAggregatedData">
      Send
    </button>
    <!-- Common scroll wrapper for both Display and mode panels -->
    <div class="scroll-wrapper">
      <div class="combined-content">
        <!-- Display component container -->
        <div class="display-wrapper">
          <Display
            :externalConfig="aggregatedData"
            @update-display-data="handleUpdateDisplayData"
          />
        </div>

        <!-- Mode panels container: arranged horizontally with no wrapping -->
        <div v-if="aggregatedData && aggregatedData.groups" class="modes-wrapper">
          <!-- Iterate over groups using computed groupsList -->
          <div
            v-for="([groupKey, group]) in groupsList"
            :key="groupKey"
            class="group-panel"
          >
            <!-- Mode panel with dropdown and button; width based on number of displays -->
            <div class="mode-panel" :style="{ width: groupWidth(group) }">
              <div class="dropdown">
                <button class="dropbtn" @click="toggleDropdown($event, groupKey)">
                  {{ group.mode || 'none' }}
                </button>
              </div>
              <button class="option-btn" @click="handleButtonClick(groupKey, group.mode)">
                <img :src="require('@/assets/gear_2.png')" class="gear-style" />
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Debug: display aggregated data -->
    <!-- <pre>{{ aggregatedData }}</pre>
    <br /><br /><br /> -->

    <!-- Teleport dropdown menu to body to always be on top -->
    <teleport to="body">
      <div v-for="([groupKey, group]) in groupsList" :key="'dropdown-' + groupKey">
        <div
          v-if="dropdownOpen[groupKey]"
          class="dropdown-content-teleport"
          :style="getDropdownStyle(groupKey)"
          @mouseleave="closeDropdown(groupKey)"
        >
          <a
            v-for="mode in modes"
            :key="mode"
            href="#"
            @click.prevent="selectMode(groupKey, mode)"
          >
            {{ mode }}
          </a>
        </div>
      </div>
    </teleport>
  </div>

  <SettingsModal
    v-if="showSettingsModal"
    :groupKey="currentModal.groupKey"
    :mode="currentModal.mode"
    :aggregatedData="aggregatedData"
    @close="showSettingsModal = false"
    @save="handleModalSave"
  />
</template>

<script setup>
import { ref, computed } from 'vue'
import Display from './Display.vue'
import SettingsModal from './SettingsModal.vue'

// Reactive variable to store display configuration data
const aggregatedData = ref(null)

// Reactive objects to store the state of dropdowns and their positions (for each group)
const dropdownOpen = ref({})
const dropdownPosition = ref({})

// Available modes
const modes = ['none', 'manual', 'mqtt', 'timer', 'clock', 'custom-api']

// Reactive variable controlling modal visibility and current data for the modal
const showSettingsModal = ref(false)
const currentModal = ref({ groupKey: null, mode: null })

// New function
// Upon receiving an updated object from Display.vue, immediately overwrite the entire aggregatedData.value.
// This ensures that every change in Display.vue (polling, click) is fully adopted without manually merging objects.
const handleUpdateDisplayData = (data) => {
  aggregatedData.value = {
    ...data,
    general: {
      ...data.general,
      groups: Object.keys(data.groups).length
    }
  }
  // Note: The "mode" of other groups (set in the configuration) was already included in data.groups, so it's preserved.
}

// Computed property to get an array of [groupKey, group] pairs
const groupsList = computed(() => {
  if (!aggregatedData.value || !aggregatedData.value.groups) return []
  return Object.entries(aggregatedData.value.groups)
})

// Calculate the width of a group's panel based on the number of displays (each display is 210px)
const groupWidth = (group) => {
  const numDisplays = group.end_position - group.start_position
  return (numDisplays + 1) * 210 + 'px'
}

// Toggle dropdown open/close state for a specific group and calculate button position
const toggleDropdown = (event, groupKey) => {
  dropdownOpen.value[groupKey] = !dropdownOpen.value[groupKey]
  if (dropdownOpen.value[groupKey]) {
    const rect = event.target.getBoundingClientRect()
    dropdownPosition.value[groupKey] = {
      top: rect.bottom,
      left: rect.left,
      width: rect.width
    }
  }
}

// Return computed style for dropdown content based on stored position
const getDropdownStyle = (groupKey) => {
  const pos = dropdownPosition.value[groupKey] || { top: 0, left: 0, width: 'auto' }
  return {
    position: 'fixed',
    top: pos.top + 'px',
    left: pos.left + 'px',
    width: pos.width + 'px',
    backgroundColor: '#e7e7e7',
    borderRadius: '7px',
    zIndex: 9999,
    color: '#333',
    fontSize: '0.9em',
    textTransform: 'uppercase',
    letterSpacing: '1px',
    fontWeight: 'bold'
  }
}

// Close dropdown for the given group key when the cursor leaves the menu area
const closeDropdown = (groupKey) => {
  dropdownOpen.value[groupKey] = false
}

// Select a mode from the dropdown and close it afterwards
const selectMode = (groupKey, mode) => {
  // Set the new mode for the given group
  aggregatedData.value.groups[groupKey].mode = mode
  dropdownOpen.value[groupKey] = false

  if (mode !== 'none') {
    currentModal.value = { groupKey, mode }
    showSettingsModal.value = true
  }
  console.log(`Mode changed in group ${groupKey}:`, aggregatedData.value.groups[groupKey].mode)
}

// Handle button click (expand logic based on mode)
const handleButtonClick = (groupKey, mode) => {
  if (mode !== 'none') {
    currentModal.value = { groupKey, mode }
    showSettingsModal.value = true
  }
  console.log('Button clicked for group:', groupKey, mode)
}

const handleModalSave = (updatedData) => {
  // After closing modal we may receive a completely new aggregatedData object
  aggregatedData.value = updatedData
  showSettingsModal.value = false
}

// Function to send aggregatedData to the ESP32 server
const sendAggregatedData = async () => {
  if (!aggregatedData.value) {
    alert('No configuration data to send.')
    return
  }

  // Measure JSON serialization time
  console.time('stringify')
  const payload = JSON.stringify(aggregatedData.value)
  console.timeEnd('stringify') // e.g., "stringify: 3ms"

  try {
    // Measure fetch time
    const start = performance.now()
    const response = await fetch('/api/v1/config', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: payload
    })
    const end = performance.now()
    console.log(`Fetch took: ${(end - start).toFixed(1)} ms`)

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`)
    }

    const result = await response.text() // server response as text
    console.log('Server response:', result)
    // optional: alert('Configuration sent successfully!')
  } catch (error) {
    console.error('Error sending configuration:', error)
    alert('Failed to send configuration.')
  }
}
</script>

<style scoped>
/* Common scroll wrapper for Display and mode panels */
.scroll-wrapper {
  overflow-x: auto; /* Enable horizontal scrolling */
  width: 100%;
  text-align: center; /* Center content */
}

/* Combined content container */
.combined-content {
  display: inline-block; /* Shrinks to fit its content */
  margin: 0 auto;
}

/* Container for Display: arranges displays inline (106px gap) */
.display-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 106px;
  margin-bottom: 20px;
}

/* Container for mode panels: horizontal no-wrap, 106px gap */
.modes-wrapper {
  display: flex;
  flex-wrap: nowrap;
  gap: 106px;
  justify-content: center;
}

/* Mode panel (dropdown + button) */
.mode-panel {
  display: flex;
  align-items: top;
}

/* Dropdown â€“ button style */
.dropdown {
  position: relative;
  display: inline-block;
  width: 100%;
}

/* Teleported menu â€“ link style with shadow */
.dropdown-content-teleport {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.dropdown-content-teleport a {
  color: #333;
  padding: 12px 5px;
  text-decoration: none;
  text-align: center;
  display: block;
}

.dropdown-content-teleport a:hover {
  background-color: #a5a5a5;
}

/* Gear button */
.option-btn {
  width: 40px;
  height: 40px;
  margin-left: 5px;
  border-radius: 7px;
  border: 3px solid #333;
  background: #d9d5c7;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.gear-style {
  scale: 0.8;
}

/* Dropdown button style */
.dropbtn {
  background-color: #333;
  border-radius: 7px;
  padding: 10px;
  cursor: pointer;
  width: 100%;
  text-align: center;
  color: #ebebeb;
  font-size: 0.9em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
}
.dropbtn:hover {
  background: #424242;
}

/* SEND button */
.submit {
  float: right;
  width: 200px;
  background: #c02b2b;
  border: 1;
  margin: 3px 3px 70px 0;
  padding: 10px;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1.1em;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
}
.submit:hover {
  background: #d63030;
}
</style>
