<template>
  <div class="display-container">
    <!-- Display background image -->
    <img :src="require('@/assets/display.png')" alt="Display background" />

    <!-- Render clickable hotspot for each segment -->
    <div
      v-for="segment in segmentsConfig"
      :key="segment.name"
      class="segment-container"
      :class="{ 'non-editable': !editable }"
      :style="{
        top: segment.top,
        left: segment.left,
        width: segment.width,
        height: segment.height,
        cursor: editable ? 'pointer' : 'default'
      }"
      @click.stop="toggleSegment(segment.name)"
    >
      <!-- If a segment is active, overlay the active-segment image.
           Use the horizontal image when rotation is 0, vertical otherwise.
           No CSS transform is applied since the image is pre-oriented. -->
      <img
        v-if="activeSegments[segment.name]"
        :src="
          segment.rotation === 0
            ? require('@/assets/segment-h.png')
            : require('@/assets/segment-v.png')
        "
        alt="Active segment"
        class="segment-image"
      />
    </div>
  </div>
</template>

<script setup>
import { reactive, ref, watch } from 'vue'

const props = defineProps({
  initialValue: {
    type: Number,
    default: 0
  },
  // If false, clicks do not toggle segments
  editable: {
    type: Boolean,
    default: true
  }
})

const emit = defineEmits(['update-signal'])

// Current bitmask value
const SignValue = ref(props.initialValue)

// State of each segment (Aâ€“G)
const activeSegments = reactive({
  A: Boolean(props.initialValue & (1 << 0)),
  B: Boolean(props.initialValue & (1 << 1)),
  C: Boolean(props.initialValue & (1 << 2)),
  D: Boolean(props.initialValue & (1 << 3)),
  E: Boolean(props.initialValue & (1 << 4)),
  F: Boolean(props.initialValue & (1 << 5)),
  G: Boolean(props.initialValue & (1 << 6))
})

// Configuration for position and size of each segment hotspot
const segmentsConfig = [
  { name: 'A', top: '19px', left: '48px', width: '115px', height: '38px', rotation: 0 },
  { name: 'B', top: '48px', left: '154px', width: '38px', height: '115px', rotation: 90 },
  { name: 'C', top: '183px', left: '154px', width: '38px', height: '115px', rotation: 90 },
  { name: 'D', top: '289px', left: '48px', width: '115px', height: '38px', rotation: 0 },
  { name: 'E', top: '183px', left: '18px', width: '38px', height: '115px', rotation: -90 },
  { name: 'F', top: '48px', left: '18px', width: '38px', height: '115px', rotation: -90 },
  { name: 'G', top: '154px', left: '48px', width: '115px', height: '38px', rotation: 0 }
]

// When props.initialValue changes, update bitmask and segment states
watch(
  () => props.initialValue,
  (newVal) => {
    SignValue.value = newVal
    activeSegments.A = Boolean(newVal & (1 << 0))
    activeSegments.B = Boolean(newVal & (1 << 1))
    activeSegments.C = Boolean(newVal & (1 << 2))
    activeSegments.D = Boolean(newVal & (1 << 3))
    activeSegments.E = Boolean(newVal & (1 << 4))
    activeSegments.F = Boolean(newVal & (1 << 5))
    activeSegments.G = Boolean(newVal & (1 << 6))
  }
)

// Helper: emit the current bitmask value to the parent
const onSignalChange = () => {
  emit('update-signal', SignValue.value)
}

// Toggle segment state (on/off) only when editable === true
function toggleSegment(segmentName) {
  if (!props.editable) return

  activeSegments[segmentName] = !activeSegments[segmentName]

  // Recalculate the new bitmask value
  let value = 0
  if (activeSegments.A) value |= 1 << 0
  if (activeSegments.B) value |= 1 << 1
  if (activeSegments.C) value |= 1 << 2
  if (activeSegments.D) value |= 1 << 3
  if (activeSegments.E) value |= 1 << 4
  if (activeSegments.F) value |= 1 << 5
  if (activeSegments.G) value |= 1 << 6

  SignValue.value = value
  onSignalChange()
}
</script>

<style scoped>
/* Display container (background) */
.display-container {
  position: relative;
  width: 210px;   /* width of display.png */
  height: 346px;  /* height of display.png */
}

/* Container for each segment hotspot.
   The cursor is set inline, so no cursor declaration is needed here. */
.segment-container {
  position: absolute;
}

/* When non-editable, force default cursor */
.non-editable {
  cursor: default !important;
}

/* Active segment image: fills the entire hotspot area */
.segment-image {
  width: 100%;
  height: 100%;
  pointer-events: none; /* clicks on the image pass through to the container */
}
</style>
