<template>
  <div class="timer-settings">
    <!-- Buttons to switch modes -->
    <div class="button-row">
      <button
        type="button"
        @click.stop="state.mode = 'simple'"
        :class="state.mode === 'simple' ? 'active-button' : 'inactive-button'"
      >
        SIMPLE TIMER
      </button>
      <button
        type="button"
        @click.stop="state.mode = 'advanced'"
        :class="state.mode === 'advanced' ? 'active-button' : 'inactive-button'"
      >
        ADVANCED TIMER
      </button>
    </div>

    <!-- Simple Timer Options -->
    <div v-if="state.mode === 'simple'" class="options">
      <div class="option">
        <label>From</label>
        <input
          type="number"
          v-model="state.simple.from"
          placeholder="e.g. 0"
          min="0"
          required
        />
      </div>
      <div class="option">
        <label>To</label>
        <input
          type="number"
          v-model="state.simple.to"
          placeholder="e.g. 10"
          min="0"
          required
        />
      </div>
      <div class="option">
        <label>Interval</label>
        <select v-model="state.simple.intervalUnit" required>
          <option value="s">seconds</option>
          <option value="m">minutes</option>
          <option value="h">hours</option>
          <option value="d">days</option>
        </select>
      </div>
      <div class="option">
        <label>Alarm</label>
        <label class="switch">
          <input type="checkbox" v-model="state.simple.alarm" />
          <span class="slider"></span>
        </label>
      </div>
    </div>

    <!-- Advanced Timer Options -->
    <div v-else class="options">
      <div class="option">
        <label>Cycles</label>
        <input
          type="number"
          v-model="state.advanced.cycles"
          min="1"
          required
        />
      </div>
      <div class="option">
        <label>Work</label>
        <input
          type="number"
          v-model="state.advanced.work"
          placeholder="e.g. 60"
          min="0"
          required
        />
      </div>
      <div class="option">
        <label>Rest</label>
        <input
          type="number"
          v-model="state.advanced.rest"
          placeholder="e.g. 30"
          min="0"
          required
        />
      </div>
      <div class="option">
        <label>Interval</label>
        <select v-model="state.advanced.intervalUnit" required>
          <option value="s">seconds</option>
          <option value="m">minutes</option>
          <option value="h">hours</option>
          <option value="d">days</option>
        </select>
      </div>
      <div class="option">
        <label>Show current cycle</label>
        <label class="switch">
          <input type="checkbox" v-model="state.advanced.showCurrentCycle" />
          <span class="slider"></span>
        </label>
      </div>
      <div class="option">
        <label>Alarm</label>
        <label class="switch">
          <input type="checkbox" v-model="state.advanced.alarm" />
          <span class="slider"></span>
        </label>
      </div>
    </div>
  </div>
</template>

<script setup>
import { onMounted, defineProps, reactive, watch } from 'vue'

const props = defineProps({
  initialConfig: {
    type: Object,
    default: () => ({})
  }
})

// Emit event to send updated settings to parent
const emit = defineEmits(['update:settings'])

const state = reactive({
  mode: 'simple',
  simple: {
    from: '0',
    to: '0',
    intervalUnit: 's',
    alarm: false
  },
  advanced: {
    cycles: '1',
    showCurrentCycle: false,
    work: '0',
    rest: '0',
    alarm: false,
    intervalUnit: 's'
  }
})

onMounted(() => {
  const cfg = props.initialConfig
  if (cfg.type === 'simple') {
    state.mode = 'simple'
    if (cfg.from) state.simple.from = cfg.from
    if (cfg.to) state.simple.to = cfg.to
    if (cfg.intervalUnit) state.simple.intervalUnit = cfg.intervalUnit
    if (cfg.alarm !== undefined) state.simple.alarm = cfg.alarm
  } else if (cfg.type === 'advanced') {
    state.mode = 'advanced'
    if (cfg.cycles) state.advanced.cycles = cfg.cycles
    if (cfg.showCurrentCycle !== undefined)
      state.advanced.showCurrentCycle = cfg.showCurrentCycle
    if (cfg.work) state.advanced.work = cfg.work
    if (cfg.rest) state.advanced.rest = cfg.rest
    if (cfg.alarm !== undefined) state.advanced.alarm = cfg.alarm
    if (cfg.intervalUnit) state.advanced.intervalUnit = cfg.intervalUnit
  }
})

// Watch only the active branch (simple or advanced) and emit flattened settings
watch(
  () => (state.mode === 'simple' ? state.simple : state.advanced),
  (activeSettings) => {
    emit('update:settings', {
      mode: 'timer',
      type: state.mode,
      ...activeSettings
    })
  },
  {
    deep: true,
    immediate: true
  }
)
</script>

<style scoped>
.button-row {
  display: flex;
  gap: 8px;
  margin-bottom: 16px;
}

button {
  flex: 1;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
  color: #ffffff;
}

.active-button {
  background-color: #007bff;
}

.inactive-button {
  background-color: #6c757d;
}

.options {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.option {
  display: flex;
  align-items: center;
  gap: 8px;
}

label {
  width: 140px;
  font-weight: bold;
}

input[type='number'],
select {
  flex: 1;
  padding: 4px 8px;
}

/* Toggle switch styles */
.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #6c757d;
  transition: 0.4s;
  border-radius: 20px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

.switch input:checked + .slider {
  background-color: #007bff;
}

.switch input:checked + .slider:before {
  transform: translateX(20px);
}
</style>
