<template>
  <div class="display-wrapper" ref="containerRef">
    <!-- Inline container for displays and separators -->
    <div class="display-inline" v-if="DisplayCount > 0">
      <template v-for="index in displayIndices" :key="index">
        <!-- SingleDisplay with click handling and editable prop -->
        <SingleDisplay
          :initialValue="getDisplayValue(index)"
          :editable="getModeForIndex(index) === 'manual'"
          @update-signal="handleUpdateSignal(index, $event)"
        />
        <!-- Inline separator (if not 'plus') -->
        <img
          v-if="index < DisplayCount && separators[index - 1] !== 'plus'"
          :src="require(`@/assets/${separators[index - 1]}.png`)"
          alt="separator"
          class="inline-separator"
          @click="userChangedSeparator(index - 1)"
        />
      </template>
    </div>

    <!-- Plus icons overlay (only when separator === 'plus') -->
    <div class="plus-overlay" v-if="DisplayCount > 0">
      <template v-for="(sep, index) in separators" :key="'overlay-' + index">
        <img
          v-if="sep === 'plus'"
          :src="require(`@/assets/plus.png`)"
          alt="plus"
          class="plus-icon"
          :style="plusStyle(index)"
          @click.stop="openContextMenu($event, index)"
        />
      </template>
    </div>

    <!-- Context menu for selecting separator -->
    <div
      v-if="contextMenu.visible"
      class="context-menu"
      :style="{ top: contextMenu.y + 'px', left: contextMenu.x + 'px' }"
    >
      <div
        v-for="option in options"
        :key="option"
        class="context-menu-option"
        @click="selectSeparator(option)"
      >
        {{ option }}
      </div>
    </div>

    <!-- Loading state -->
    <div v-if="DisplayCount === 0">
      Loading...
    </div>
  </div>
</template>

<script setup>
import { ref, computed, watch, onMounted, onUnmounted } from 'vue'
import SingleDisplay from './SingleDisplay.vue'

// Prop: externalConfig from parent (Mode.vue)
const props = defineProps({
  externalConfig: {
    type: Object,
    default: null
  }
})

// Emit to parent
const emit = defineEmits(['update-display-data'])

// Reference to the container
const containerRef = ref(null)

// Main settings (fetched from server first, then updated via externalConfig)
const jsonSettings = ref(null)

// Total number of displays
const DisplayCount = ref(0)

// Array of separators (e.g., 'space', 'colon', 'plus', etc.)
const separators = ref([])

// Values from API (/api/v1/pattern)
const SignValues = ref([])

// Options for context menu (separators)
const options = ['space', 'blank', 'colon', 'dot', 'dash']

// Context menu state
const contextMenu = ref({
  visible: false,
  x: 0,
  y: 0,
  index: null
})

// Constants for plusStyle calculations
const displayWidth = 210
const plusWidth = 30
const displayHeight = 346
const plusTop = (displayHeight - plusWidth) / 2

// Fetch initial display settings from /api/v1/config
async function fetchDisplaySettings() {
  try {
    //const response = await fetch('http://localhost:3000/display_settings');
    const response = await fetch('/api/v1/config')
    if (!response.ok) {
      throw new Error('Network response was not ok: ' + response.statusText)
    }
    const data = await response.json()
    jsonSettings.value = data

    // Compute number of displays
    const groupValues = Object.values(jsonSettings.value.groups)
    const count = Math.max(...groupValues.map((g) => g.end_position)) + 1
    DisplayCount.value = count

    // Initialize separators as 'plus'
    separators.value = Array(count - 1).fill('plus')
    // Overwrite actual separators from config
    for (const key in jsonSettings.value.groups) {
      const group = jsonSettings.value.groups[key]
      if (group.start_position > 0 && group.separator) {
        separators.value[group.start_position - 1] = group.separator
      }
    }

    // Initialize SignValues as all null (first render will use config)
    SignValues.value = Array(count).fill(null)

    // Emit initial aggregatedData so Mode.vue receives it immediately
    emit('update-display-data', aggregateDisplaySettings())
  } catch (error) {
    console.error('Error fetching display settings:', error)
  }
}

// Poll /api/v1/pattern every second; update SignValues where mode != 'manual'
let pollIntervalId = null
async function pollPattern() {
  try {
    const res = await fetch('/api/v1/pattern')
    if (!res.ok) {
      throw new Error('Pattern fetch failed: ' + res.status)
    }
    const arr = await res.json()

    let anyChanged = false
    for (let i = 0; i < arr.length && i < SignValues.value.length; i++) {
      const index = i + 1
      const mode = getModeForIndex(index)
      if (mode === 'manual') continue
      if (SignValues.value[i] !== arr[i]) {
        SignValues.value[i] = arr[i]
        anyChanged = true
      }
    }
    if (arr.length > SignValues.value.length) {
      for (let i = SignValues.value.length; i < arr.length; i++) {
        const index = i + 1
        const mode = getModeForIndex(index)
        if (mode === 'manual') continue
        SignValues.value[i] = arr[i]
        anyChanged = true
      }
    }

    // If any SignValues changed (for non-'manual' modes), emit updated aggregatedData
    if (anyChanged) {
      emit('update-display-data', aggregateDisplaySettings())
    }
  } catch (e) {
    console.error('[Display.vue] pollPattern error:', e)
  }
}

// On mount: fetch config, then start polling
onMounted(() => {
  fetchDisplaySettings().then(() => {
    pollPattern()
    pollIntervalId = setInterval(pollPattern, 1000)
  })
})

// Clean up on unmount
onUnmounted(() => {
  if (pollIntervalId) {
    clearInterval(pollIntervalId)
    pollIntervalId = null
  }
})

// Computed array [1, 2, ..., DisplayCount]
const displayIndices = computed(() =>
  Array.from({ length: DisplayCount.value }, (_, i) => i + 1)
)

// Helper: get initial value for a display from config (no polling)
function getInitialValueFromConfig(index) {
  if (!jsonSettings.value) return 0
  const idx = index - 1
  const groups = Object.values(jsonSettings.value.groups).sort(
    (a, b) => a.start_position - b.start_position
  )
  for (const group of groups) {
    if (idx >= group.start_position && idx <= group.end_position) {
      const localIndex = idx - group.start_position
      return group.pattern?.[`disp${localIndex}`] ?? 0
    }
  }
  return 0
}

// Helper: get mode for a given display index
function getModeForIndex(index) {
  if (!jsonSettings.value) return 'none'
  const idx = index - 1
  const groups = Object.values(jsonSettings.value.groups).sort(
    (a, b) => a.start_position - b.start_position
  )
  for (const group of groups) {
    if (idx >= group.start_position && idx <= group.end_position) {
      return group.mode ?? 'none'
    }
  }
  return 'none'
}

// Helper: get value to show on display
function getDisplayValue(index) {
  const idx0 = index - 1
  const mode = getModeForIndex(index)
  const currentValue = SignValues.value[idx0]
  if (mode === 'manual') {
    // In manual mode, use SignValues if defined, otherwise fallback to config
    return currentValue !== null && currentValue !== undefined
      ? currentValue
      : getInitialValueFromConfig(index)
  }
  // In other modes, also prefer SignValues, otherwise fallback to config
  if (currentValue !== null && currentValue !== undefined) {
    return currentValue
  }
  return getInitialValueFromConfig(index)
}

// Build aggregatedData object from SignValues and separators
function aggregateDisplaySettings() {
  const totalDisplays = DisplayCount.value
  const computedGroups = []
  let currentGroupStart = 0

  for (let i = 0; i < totalDisplays - 1; i++) {
    if (separators.value[i] !== 'plus') {
      const groupPattern = {}
      for (let j = currentGroupStart; j <= i; j++) {
        const currentValue = SignValues.value[j]
        const mode = getModeForIndex(j + 1)
        groupPattern[`disp${j - currentGroupStart}`] =
          currentValue !== null && currentValue !== undefined
            ? currentValue
            : getInitialValueFromConfig(j + 1)
      }
      computedGroups.push({
        start: currentGroupStart,
        end: i,
        pattern: groupPattern,
        separator: currentGroupStart > 0 ? separators.value[currentGroupStart - 1] : null
      })
      currentGroupStart = i + 1
    }
  }

  // Last group
  const lastPattern = {}
  for (let j = currentGroupStart; j < totalDisplays; j++) {
    const currentValue = SignValues.value[j]
    const mode = getModeForIndex(j + 1)
    lastPattern[`disp${j - currentGroupStart}`] =
      currentValue !== null && currentValue !== undefined
        ? currentValue
        : getInitialValueFromConfig(j + 1)
  }
  computedGroups.push({
    start: currentGroupStart,
    end: totalDisplays - 1,
    pattern: lastPattern,
    separator: currentGroupStart > 0 ? separators.value[currentGroupStart - 1] : null
  })

  // Compare with original jsonSettings.value.groups to preserve start/end, separator, mode
  const originalGroups = jsonSettings.value.groups
  const originalEntries = Object.entries(originalGroups).sort(
    (a, b) => a[1].start_position - b[1].start_position
  )

  const updatedGroups = {}
  for (let i = 0; i < computedGroups.length; i++) {
    const computed = computedGroups[i]
    let matchingOriginal = null
    for (const [key, origGroup] of originalEntries) {
      if (
        origGroup.start_position === computed.start &&
        origGroup.end_position === computed.end
      ) {
        matchingOriginal = origGroup
        break
      }
    }

    let newGroup = {}
    if (matchingOriginal) {
      newGroup = { ...matchingOriginal }
      // Overwrite pattern and separator from computed
      newGroup.pattern = computed.pattern
      newGroup.separator =
        computed.start > 0 ? separators.value[computed.start - 1] : null
      newGroup.start_position = computed.start
      newGroup.end_position = computed.end
    } else {
      // If no original match (rare), create a new group structure
      newGroup = {
        start_position: computed.start,
        end_position: computed.end,
        pattern: computed.pattern,
        separator:
          computed.start > 0 ? separators.value[computed.start - 1] : null,
        mode: null
      }
    }
    updatedGroups[`group${i}`] = newGroup
  }

  return {
    general: { ...jsonSettings.value.general, groups: computedGroups.length },
    groups: updatedGroups
  }
}

// Handle signal update from SingleDisplay when in 'manual' mode
function handleUpdateSignal(index, newValue) {
  if (getModeForIndex(index) !== 'manual') {
    return
  }
  const idx0 = index - 1
  SignValues.value[idx0] = newValue
  // Emit only when user has clicked a segment
  emit('update-display-data', aggregateDisplaySettings())
}

// Handle inline separator click: set to 'plus' and emit update
function userChangedSeparator(idx) {
  separators.value[idx] = 'plus'
  emit('update-display-data', aggregateDisplaySettings())
}

// Handle separator selection from context menu
function selectSeparator(option) {
  if (contextMenu.value.index !== null) {
    separators.value[contextMenu.value.index] = option
    emit('update-display-data', aggregateDisplaySettings())
  }
  contextMenu.value.visible = false
  contextMenu.value.index = null
}

// Open context menu
function openContextMenu(event, index) {
  event.preventDefault()
  const rect = containerRef.value.getBoundingClientRect()
  contextMenu.value = {
    visible: true,
    x: event.clientX - rect.left,
    y: event.clientY - rect.top,
    index: index
  }
}

// Global listener to close context menu when clicking elsewhere
document.addEventListener('click', () => {
  contextMenu.value.visible = false
})

// Calculate style for plus icons
function plusStyle(index) {
  let offset = 0
  for (let i = 0; i < index; i++) {
    if (separators.value[i] !== 'plus') {
      offset += 106 // width of separator icon
    }
  }
  const left = (index + 1) * displayWidth + offset - plusWidth / 2
  return { left: left + 'px', top: plusTop + 'px' }
}

// When parent (Mode.vue) provides new aggregatedData (e.g., mode or separator change),
// overwrite jsonSettings and separators. Do not reset SignValues except for changed modes.
watch(
  () => props.externalConfig,
  (newConfig, oldConfig) => {
    if (!newConfig) return

    // If previous jsonSettings exists, compare modes in groups
    if (jsonSettings.value) {
      const oldGroups = jsonSettings.value.groups
      const newGroups = newConfig.groups
      // For each group, check if mode changed
      for (const key in newGroups) {
        const oldMode = oldGroups[key]?.mode ?? 'none'
        const newMode = newGroups[key]?.mode ?? 'none'
        if (oldMode !== newMode) {
          // Clear SignValues for this group's range
          const { start_position, end_position } = newGroups[key]
          for (let idx = start_position; idx <= end_position; idx++) {
            SignValues.value[idx] = null
          }
        }
      }
    }

    // Overwrite jsonSettings with new config
    jsonSettings.value = JSON.parse(JSON.stringify(newConfig))

    // Recalculate DisplayCount (in case group ranges changed)
    const groupValues = Object.values(jsonSettings.value.groups)
    const count = Math.max(...groupValues.map((g) => g.end_position)) + 1
    DisplayCount.value = count

    // Reinitialize separators (default 'plus')
    separators.value = Array(count - 1).fill('plus')
    for (const key in jsonSettings.value.groups) {
      const grp = jsonSettings.value.groups[key]
      if (grp.start_position > 0 && grp.separator) {
        separators.value[grp.start_position - 1] = grp.separator
      }
    }

    // Do not reset SignValues here (except where mode changed above)
  },
  { deep: true, immediate: true }
)
</script>

<style scoped>
.display-wrapper {
  position: relative;
  width: fit-content;
  margin: 0 auto;
}

/* Container for displays arranged inline */
.display-inline {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}

/* Inline separator (non-plus) styles */
.inline-separator {
  width: 106px;
  cursor: pointer;
}

/* Container for plus icons overlayed on displays */
.plus-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* Only plus icons receive clicks */
}

/* Plus icon styles */
.plus-icon {
  position: absolute;
  width: 30px;
  cursor: pointer;
  pointer-events: auto; /* Allow clicks on plus icons */
}

/* Context menu styles */
.context-menu {
  position: absolute;
  border: 1px solid #ccc;
  background-color: #fff;
  border-radius: 5px;
  z-index: 1000;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  font-size: 0.8em;
  text-transform: uppercase;
  width: 85px;
  color: #222222;
  letter-spacing: 1px;
  font-weight: 700;
}

.context-menu :hover {
  background-color: #b8b8b8;
}

.context-menu-option {
  padding: 5px;
  cursor: pointer;
}

/* Loading state is unstyled and displays default text */
</style>
