<template>
  <div class="api-config-component">
    <div class="form-field">
      <label>Endpoint URL:</label>
      <input
        v-model="endpointUrl"
        type="url"
        placeholder="https://api.example.com/endpoint"
      />
    </div>
    <div class="form-field">
      <label>HTTP Method:</label>
      <select v-model="httpMethod">
        <option>GET</option>
        <option>POST</option>
      </select>
    </div>
    <div class="form-field">
      <label>Headers (JSON):</label>
      <textarea
        v-model="headersText"
        placeholder='{"Authorization": "Bearer TOKEN", "Accept": "application/json"}'
      ></textarea>
    </div>
    <div class="form-field" v-if="httpMethod === 'POST'">
      <label>Request Body (JSON):</label>
      <textarea
        v-model="requestBody"
        placeholder='{"key": "value"}'
      ></textarea>
    </div>
    <div class="form-field">
      <label>Response Format:</label>
      <select v-model="responseFormat">
        <option value="json">JSON</option>
        <option value="xml">XML</option>
        <option value="text">Text</option>
      </select>
    </div>
    <div class="form-field">
      <label>Response Key Path:</label>
      <input
        v-model="responseKeyPath"
        type="text"
        placeholder="data.field.subfield"
      />
    </div>
    <div class="form-field">
      <label>Polling Interval (minutes):</label>
      <input v-model.number="pollInterval" type="number" min="1" />
    </div>
    <div class="form-actions">
      <button type="submit">SAVE</button>
      <button @click="testFetch" :disabled="isTesting">Test</button>
      <button type="button" @click="closeModal">CANCEL</button>
    </div>

    <!-- Modal for test result -->
    <div
      v-if="showTestModal"
      class="modal-backdrop"
      @mousedown.self="closeTestModal"
    >
      <div class="modal">
        <h4>Test Result</h4>
        <div class="modal-content">
          <pre>{{ formattedModalContent }}</pre>
          <button @click="closeTestModal">OK</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, defineEmits, defineExpose, computed, onMounted } from 'vue'

const props = defineProps({
  initialConfig: {
    type: Object,
    default: () => ({})
  }
})

// Reactive state for form fields
const endpointUrl     = ref('')
const httpMethod      = ref('GET')
const headersText     = ref('')
const requestBody     = ref('')
const responseFormat  = ref('json')
const responseKeyPath = ref('')
const pollInterval    = ref(60)

// State for testing and modal
const isTesting       = ref(false)
const testResult      = ref(null)
const testError       = ref('')
const showTestModal   = ref(false)
const modalContent    = ref(null)

// Emit configuration updates and close event to parent
const emits = defineEmits(['update:config', 'close'])

// Initial initialization of fields from initialConfig
onMounted(() => {
  const cfg = props.initialConfig
  if (cfg.endpoint)         endpointUrl.value     = cfg.endpoint
  if (cfg.method)           httpMethod.value      = cfg.method
  if (cfg.headers)          headersText.value     = JSON.stringify(cfg.headers, null, 2)
  if (cfg.body != null)     requestBody.value     = JSON.stringify(cfg.body, null, 2)
  if (cfg.responseFormat)   responseFormat.value  = cfg.responseFormat
  if (cfg.responseKeyPath)  responseKeyPath.value = cfg.responseKeyPath
  if (cfg.interval)         pollInterval.value    = cfg.interval
})

function closeModal() {
  emits('close')
}

function buildConfigPayload() {
  let headersObj = {}
  if (headersText.value) {
    try {
      headersObj = JSON.parse(headersText.value)
    } catch (e) {
      throw new Error('Error parsing headers: ' + e.message)
    }
  }
  let bodyObj = null
  if (httpMethod.value === 'POST' && requestBody.value) {
    try {
      bodyObj = JSON.parse(requestBody.value)
    } catch (e) {
      throw new Error('Error parsing body: ' + e.message)
    }
  }
  return {
    endpoint: endpointUrl.value,
    method: httpMethod.value,
    headers: headersObj,
    body: bodyObj,
    responseFormat: responseFormat.value,
    responseKeyPath: responseKeyPath.value,
    interval: pollInterval.value
  }
}

defineExpose({ getConfig: buildConfigPayload })

// Save configuration and emit to parent
function sendConfigToDevice() {
  try {
    const configPayload = buildConfigPayload()
    emits('update:config', configPayload)
  } catch (e) {
    testError.value = e.message
    openModalWithContent(e.message)
  }
}

// Test connection and display result in modal
async function testFetch() {
  testError.value  = ''
  testResult.value = null
  isTesting.value  = true

  try {
    const options = { method: httpMethod.value, headers: {} }
    if (headersText.value) {
      options.headers = JSON.parse(headersText.value)
    }
    if (httpMethod.value === 'POST') {
      options.headers['Content-Type'] = 'application/json'
      if (requestBody.value) {
        options.body = JSON.stringify(JSON.parse(requestBody.value))
      }
    }

    const response = await fetch(endpointUrl.value, options)
    if (!response.ok) throw new Error(`HTTP ${response.status}`)

    let data =
      responseFormat.value === 'text'
        ? await response.text()
        : responseFormat.value === 'xml'
          ? new DOMParser().parseFromString(await response.text(), 'application/xml')
          : await response.json()

    let result = data
    if (responseKeyPath.value) {
      responseKeyPath.value.split('.').forEach((key) => {
        result = result[key]
      })
    }
    testResult.value = result
  } catch (error) {
    testError.value = error.message
  } finally {
    isTesting.value = false
    const content =
      testError.value ||
      (typeof testResult.value === 'object'
        ? JSON.stringify(testResult.value, null, 2)
        : testResult.value)
    openModalWithContent(content)
  }
}

// Helper to open modal with given content
function openModalWithContent(content) {
  modalContent.value  = content
  showTestModal.value = true
}

// Close test result modal
function closeTestModal() {
  showTestModal.value = false
}

// Computed for formatted modal content
const formattedModalContent = computed(() => modalContent.value)
</script>

<style>
.api-config-component {
  max-width: 420px;
  background: #ffffff;
  text-align: left;
  border-radius: 10px;
}

label {
  color: #333333;
  display: inline-block;
  margin: 25px 0 15px;
  font-size: 0.6em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
}

input,
select,
textarea {
  display: block;
  padding: 10px 6px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #dddddd;
  color: #555555;
}

button {
  background: #40464e;
  border: none;
  width: 80px;
  padding: 10px 20px;
  margin-top: 20px;
  color: #ffffff;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.7em;
  text-transform: uppercase;
}

.form-actions {
  display: flex;
  justify-content: space-between;
}

.error {
  color: #ff0062;
  margin-top: 10px;
  font-size: 0.8em;
  font-weight: bold;
}

/* Modal styles */
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background: #ffffff;
  padding: 20px;
  border-radius: 10px;
  max-width: 500px;
  width: 100%;
}

.modal-content {
  text-align: left;
}

.modal-content pre {
  background: #f5f5f5;
  padding: 10px;
  border-radius: 5px;
  text-align: left;
  max-height: 200px;
  overflow: auto;
}

.modal-content button {
  margin-top: 20px;
  background: #40464e;
  color: #ffffff;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 0.7em;
  text-transform: uppercase;
}
</style>
