<template>
  <div class="backdrop" @mousedown.self="closeModal">
    <div class="modal">
      <!-- MQTT mode -->
      <form @submit.prevent="save" v-if="mode === 'mqtt'">
        <h3>MQTT Settings</h3>
        <div class="information-style">
          <img :src="require('@/assets/information-icon.png')" alt="Information" />
          <p>Begin by enabling MQTT mode. Next, enter the MQTT broker information and user credentials (if applicable). The final step is to specify the TOPICâ€”it must match exactly the one that will be sent (for example, from Home Assistant).</p>
        </div>
        <MqttData @close="closeModal" />
        <label for="topicInput">Topic:</label>
        <input type="text" id="topicInput" v-model="topicValue" required />
        <div class="button-row">
          <button type="submit">SAVE</button>
          <button type="button" @click="closeModal">CANCEL</button>
        </div>
      </form>

      <!-- CLOCK mode -->
      <form @submit.prevent="save" v-if="mode === 'clock'">
        <h3>Clock Settings</h3>
        <div class="information-style">
          <img :src="require('@/assets/information-icon.png')" alt="Information" />
          <p>First, select the appropriate time zone (itâ€™s set globally, so it will apply when you choose this mode for another group). If needed, set the hour offset and choose either 12-hour or 24-hour format. Finally, select what you want to display: minutes, hours, seconds, days, months or years.</p>
        </div>
        <TimeMode
          ref="timeModeRef"
          :initialConfig="groupData"
          :initialGeneralConfig="general"
          @unitSelected="clockUnit = $event"
          @formatSelected="timeFormat = $event"
          @timezoneSelected="onTimezoneSelected"
          @offsetSelected="onOffsetSelected"
        />
        <div class="button-row">
          <button type="submit">SAVE</button>
          <button type="button" @click="closeModal">CANCEL</button>
        </div>
      </form>

      <!-- TIMER mode -->
      <form @submit.prevent="save" v-if="mode === 'timer'">
        <h3>Timer Settings</h3>
        <div class="information-style">
          <img :src="require('@/assets/information-icon.png')" alt="Information" />
          <p>The SIMPLE TIMER counts from any starting value to any ending value. It can count up or down. The interval defines how often the value changes. An alarm will sound when the timer completes. The ADVANCED TIMER lets you set the work duration, break duration, and number of cycles. You can also display the current cycle before the timer itself.
</p>
        </div>
        <TimerMode :initialConfig="groupData" @update:settings="onSettingsUpdate" />
        <div class="button-row">
          <button type="submit">SAVE</button>
          <button type="button" @click="closeModal">CANCEL</button>
        </div>
      </form>

      <!-- MANUAL mode -->
      <form @submit.prevent="save" v-if="mode === 'manual'">
        <h3>Manual Settings</h3>
        <div class="information-style">
          <img :src="require('@/assets/information-icon.png')" alt="Information" />
          <p>To change what appears on the display, click on individual segments to toggle them on or off.</p>
        </div>
        <div class="button-row">
          <button type="submit">OK</button>
        </div>
      </form>

      <!-- CUSTOM API mode -->
      <form @submit.prevent="save" v-if="mode === 'custom-api'">
        <h3>Custom API (advanced)</h3>
        <div class="information-style">
          <img :src="require('@/assets/information-icon.png')" alt="Information" />
          <p>To fetch information from any API, provide its URL, then select the HTTP method. If access to this API is protected by a key, include it in the HEADERS section. To extract the specific value from the full JSON object, enter its path in the RESPONSE KEY PATH field. Finally, specify how often this value should be retrieved.</p>
        </div>
        <GeneralApi ref="apiModeRef" :initialConfig="groupData" @close="closeModal" />
      </form>
    </div>
  </div>
</template>

<script setup>
import { defineProps, defineEmits, ref, onMounted, computed } from 'vue'
import TimeMode from './TimeMode.vue'
import GeneralApi from './GeneralApi.vue'
import TimerMode from './TimerMode.vue'
import MqttData from './MqttData.vue'

// Prop: externalConfig from parent (Mode.vue)
const props = defineProps({
  groupKey: { type: String, required: true },
  mode: { type: String, required: true },
  aggregatedData: { type: Object, required: true }
})

// Emit events to parent
const emits = defineEmits(['close', 'save'])

// Computed: data for this group
const groupData = computed(() => props.aggregatedData.groups[props.groupKey] || {})
// Computed: global settings
const general = computed(() => props.aggregatedData.general || {})
// Reactive for global timezone
const timeZone = ref(general.value.time_zone || '')
// Reactive for offset value
const offsetValue = ref(groupData.value.offset ?? 0)

// Handle timezone selected in TimeMode child
function onTimezoneSelected(tz) {
  timeZone.value = tz
}

// Handle offset selected in TimeMode child
function onOffsetSelected(val) {
  offsetValue.value = val
}

// Reactive vars for user input and child refs
const topicValue = ref('')
const clockUnit = ref('hours')
const timeFormat = ref('24h')
const timeModeRef = ref(null)
const apiModeRef = ref(null)

// Reactive holder for TimerMode data
const timerSettings = ref({
  mode: 'simple',
  simple: {
    from: '',
    to: '',
    intervalValue: '',
    intervalUnit: 'seconds',
    alarm: false
  },
  advanced: {
    cycles: '',
    showCurrentCycle: false,
    work: '',
    rest: '',
    alarm: false,
    intervalValue: '',
    intervalUnit: 'seconds'
  }
})

// Initialize topicValue from aggregatedData
function initTopic() {
  if (props.mode === 'mqtt') {
    const group = props.aggregatedData.groups[props.groupKey] || {}
    topicValue.value = group.topic ?? ''
  }
}

onMounted(() => {
  initTopic()
})

// Handle updated settings from TimerMode child
const onSettingsUpdate = (settings) => {
  timerSettings.value = settings
}

// Close the modal
const closeModal = () => {
  emits('close')
}

// Save handler: clean up and assign values based on mode
const save = () => {
  const group = props.aggregatedData.groups[props.groupKey]
  const baseKeys = ['start_position', 'end_position', 'pattern', 'separator', 'mode']

  if (props.mode === 'mqtt') {
    const allowed = [...baseKeys, 'topic']
    Object.keys(group).forEach(key => {
      if (!allowed.includes(key)) delete group[key]
    })
    group.topic = topicValue.value

  } else if (props.mode === 'clock') {
    const allowed = [...baseKeys, 'clock_unit', 'time_format', 'time_zone', 'offset']
    Object.keys(group).forEach(key => {
      if (!allowed.includes(key)) delete group[key]
    })
    group.clock_unit = clockUnit.value
    group.time_format = timeFormat.value
    group.offset = offsetValue.value
    props.aggregatedData.general.time_zone = timeZone.value

    if (timeModeRef.value) timeModeRef.value.sendTimeArray()

  } else if (props.mode === 'timer') {
    // Determine allowed keys based on timerSettings
    const settingKeys = Object.keys(timerSettings.value).filter(key => key !== 'mode')
    const allowed = [...baseKeys, 'mode', ...settingKeys]
    Object.keys(group).forEach(key => {
      if (!allowed.includes(key)) delete group[key]
    })
    group.mode = timerSettings.value.mode
    const { mode, ...rest } = timerSettings.value
    Object.assign(group, rest)

  } else if (props.mode === 'custom-api') {
    const allowed = [...baseKeys, 'endpoint', 'method', 'headers', 'body', 'responseFormat', 'responseKeyPath', 'interval']
    Object.keys(group).forEach(key => {
      if (!allowed.includes(key)) delete group[key]
    })
    if (apiModeRef.value && apiModeRef.value.getConfig) {
      const apiConfig = apiModeRef.value.getConfig()
      Object.assign(group, apiConfig)
    }

  } else if (props.mode === 'manual') {
    const allowed = [...baseKeys, 'endpoint', 'method', 'headers', 'body', 'responseFormat', 'responseKeyPath', 'interval']
    Object.keys(group).forEach(key => {
      if (!allowed.includes(key)) delete group[key]
    })
  }

  emits('save', props.aggregatedData)
}
</script>

<style scoped>
.modal {
  width: 400px;
  padding: 20px;
  margin: 100px auto;
  background: #ffffff;
  border-radius: 10px;
  position: relative;
  z-index: 10000;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.button-row {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 20px;
}

button {
  background: #40464e;
  border: none;
  width: 80px;
  padding: 10px 20px;
  margin-top: 20px;
  color: white;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.7em;
  text-transform: uppercase;
}

img {
  width: 20px;
  height: auto;
  object-fit: contain;
  margin: 10px 10px 0 10px;
}

.information-style {
  display: flex;
  align-items: flex-start;
  color: #000000;
  border-radius: 10px;
  font-size: 0.8em;
  background-color: antiquewhite;
}

label {
  color: #333;
  display: inline-block;
  margin: 25px 0 15px;
  font-size: 0.6em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
}

input,
select {
  display: block;
  padding: 10px 6px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #9c9c9c;
  color: #383838;
}
</style>
